import React, { useState, useEffect } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import moment from 'moment'

const localizer = momentLocalizer(moment);

const Tcalendar = () => {
  const [events, setEvents] = useState([]);
  useEffect(() => fetchData(), []);

  const fetchData = () => {
    fetch(`https://customerrest.herokuapp.com/gettrainings`)
      .then(response => response.json())
      .then(data => {
        return setEvents(
          data.map(event => ({
            title: event.activity + (" : ") + event.customer.firstname + (" ") + event.customer.lastname,
            // ^ This isn't work if someone adds the training without the customer.
            start: new Date(event.date),
            end: new Date(moment(event.date).add(event.duration, "minutes"))
          }))
        );
      });
  };

  return ( 
    <div>
    <Calendar localizer = {localizer}
    events = {events}
    startAccessor = "start"
    endAccessor = "end"
    style = {{height: "85vh"}}
    /> </div>
  );
};
export default Tcalendar;