import React, { useState, useEffect } from 'react';
import ReactTable from 'react-table-v6';
import 'react-table-v6/react-table.css';
import { Typography, Button} from 'antd';
import { DeleteTwoTone, PlusCircleTwoTone } from '@ant-design/icons';

import Addcustomer from './Addcustomer';
import Addtraining from './Addtraining';
import Editcustomer from './Editcustomer';

const { Title } = Typography;

export default function Customerlist() {
    const [customer, setcustomer] = useState([]);

    useEffect (() => fetchData(), []);

    const fetchData = () => {
        fetch('https://customerrest.herokuapp.com/api/customers')
        .then(response => response.json())
        .then(data => setcustomer(data.content));
    }
    
    const deleteCustomer = (link) => {
        if (window.confirm('Are you sure?')){
            fetch(link, {method: 'DELETE'})
            .then(res => fetchData())
            .catch(err => console.error(err))
            }
          }

    const saveCustomer = (customer) => {
        fetch('https://customerrest.herokuapp.com/api/customers', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(customer)
        })
        .then(res => fetchData())
        .catch(err => console.error(err))
      }

      const updateCustomer = (customer, link) => {
        fetch(link, {
        method: 'PUT',
        headers : {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(customer)
        })
        .then(res => fetchData())
        .catch(err => console.error(err))
      }

      const addTraining = (training) => {
        if (window.confirm('Here could be "Add training to customer" - functionðŸŒš')){
        fetch('https://customerrest.herokuapp.com/api/customers/{id}/trainings', {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(training)
            })
        .catch(err => console.error(err))
        }
      }

    const columns = [
        {
            Header: props => <Button type="primary" block>First name</Button>,
            accessor: 'firstname'
        },
        {
            Header: props => <Button type="primary" block>Last name</Button>,
            accessor: 'lastname'
        },
        {
            Header: props => <Button type="primary" block>Street address</Button>,
            accessor: 'streetaddress'
        },
        {
            Header: props => <Button type="primary" block>Postcode</Button>,
            accessor: 'postcode'
        },
        {
            Header: props => <Button type="primary" block>City</Button>,
            accessor: 'city'
        },
        {
            Header: props => <Button type="primary" block>E-mail</Button>,
            accessor: 'email'
        },
        {
            Header: props => <Button type="primary" block>Phone</Button>,
            accessor: 'phone'
        },
        {
            sortable: false,
            filterable: false,
            width: 50,
            Cell: row => <Editcustomer updateCustomer={updateCustomer} customer={row.original} />
          },
        {
            sortable: false,
            filterable: false,
            width: 50,
            accessor: 'links[0].href',
            Cell: row =>
            //<Button onClick={() => deleteCustomer(row.value)}>X</Button>
            <Button type="text" icon={<DeleteTwoTone twoToneColor="#f5222d" />} onClick={() => deleteCustomer(row.value)} />
          },
          {
            sortable: false,
            filterable: false,
            width: 50,
            accessor: 'links[0].href',
            Cell: row =>
            //<Button onClick={() => addTraining(row.value) }>Add</Button>
            <Button type="text" icon={<PlusCircleTwoTone/>} onClick={() => addTraining(row.value) }/>
          }
    ]

return(
<div>
    <Title level={4}>All customers</Title>
    <Addcustomer saveCustomer={saveCustomer} />
    <ReactTable filterable={true} showPagination={false} minRows ={0} data={customer} columns={columns} />
</div>
);
}