import React, { useState, useEffect } from 'react';
import ReactTable from 'react-table-v6';
import 'react-table-v6/react-table.css';
import moment from 'moment'
import { Typography, Button} from 'antd';
import { DeleteTwoTone } from '@ant-design/icons';

import Addtraining from './Addtraining';
import Edittraining from './Edittraining';

const { Title } = Typography;
  
export default function Traininglist() {
    const [training, setTraining] = useState([]);
    useEffect (() => fetchData(), []);

    const fetchData = () => {
        fetch('https://customerrest.herokuapp.com/api/trainings')
        .then(response => response.json())
        .then(data => setTraining(data.content))
        .catch(err => console.error(err))
    }

    const deleteTraining = (link) => {
        if (window.confirm('Are you sure?')){
        fetch(link, {method: 'DELETE'})
        .then(res => fetchData())
        .catch(err => console.error(err))
        }
      }

    const saveTraining = (training) => {
        fetch('https://customerrest.herokuapp.com/api/trainings', {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json'
            },
              body: JSON.stringify(training)
            })
        .then(res => fetchData())
        .catch(err => console.error(err))
        }

        const updateTraining = (training, link) => {
            fetch(link, {
            method: 'PUT',
            headers : {'Content-Type': 'application/json'},
            body: JSON.stringify(training)
            })
            .then(res => fetchData())
            .catch(err => console.error(err))
          }
          

    const columns = [
        {
            Header: props => <Button type="primary" block>Date</Button>,
            accessor: 'date',
            Cell: row => moment(row.value).format('DD/MM/YYYY')
        },
        {
            Header: props => <Button type="primary" block>Duration</Button>,
            accessor: 'duration',
        },
        {
            Header: props => <Button type="primary" block>Activity</Button>,
            accessor: 'activity'
        },
        {
            sortable: false,
            filterable: false,
            width: 50,
            Cell: row => <Edittraining updateTraining={updateTraining} training={row.original} />
          },
        {
            sortable: false,
            filterable: false,
            width: 50,
            accessor: 'links[0].href',
            Cell: row => 
           <Button type="text" icon={<DeleteTwoTone twoToneColor="#f5222d" />} onClick={() => deleteTraining(row.value)} />
        }
    ]

return(
<div>
    <Title level={4}>Trainings</Title>
    <Addtraining saveTraining={saveTraining} />
    <ReactTable filterable={true} showPagination={false} minRows ={0} data={training} columns={columns} />
</div>
);
}



