import React from 'react';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import moment from 'moment'
import { Button } from 'antd';
import { EditTwoTone } from '@ant-design/icons';

export default function Edittraining(props){
    const [open, setOpen] = React.useState(false);
    const [training, setTraining] = React.useState({
        date: '', duration: '', activity: ''
    })

    const handleClickOpen = () => {
        setTraining({
          date: props.training.date,
          duration: props.training.duration,
          activity: props.training.activity
        })
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleInputChange = (event) => {
        setTraining({...training, [event.target.name]: event.target.value })
    }

    const dateChange = date => {
        setTraining({
          ...training,
          date: date
        });
      };

    const updateTraining = () => {
        props.updateTraining(training, props.training);
        handleClose();
    }

    return(
    <div>
        <Button type="text" icon={<EditTwoTone twoToneColor/>} onClick={handleClickOpen} />
      
        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
          <DialogTitle id="form-dialog-title">Edit training</DialogTitle>
          <DialogContent>
   
            <TextField
              autoFocus
              margin="dense"
              name="date"
              value={training.date}
              onChange={date => dateChange(moment(date).format('YYYY-MM-DD'))}
              label="Date"
              fullWidth
            />
            <TextField
              autoFocus
              margin="dense"
              name="duration"
              value={training.duration}
              onChange={e => handleInputChange(e)}
              label="Duration"
              fullWidth
            />
            <TextField
              autoFocus
              margin="dense"
              name="activity"
              value={training.activity}
              onChange={e => handleInputChange(e)}
              label="Activity"
              fullWidth
            />
            
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
            <Button onClick={updateTraining} color="primary">
              Save
            </Button>
          </DialogActions>
        </Dialog>
    </div>

    );
}