import React from 'react';
import './App.css';
import { BrowserRouter, Route, Switch, Link } from "react-router-dom";
import Home from './Home';
import Customerlist from './components/Customerlist';
import Traininglist from './components/Traininglist';
import Customertraining from './components/Customertraining';
import 'antd/dist/antd.css';
import { Menu, Typography } from 'antd';
import Tcalendar from './components/Tcalendar';

const { Title } = Typography;

function App() {
  return (
   <div className="App">
     
<BrowserRouter>
  <div> 
    <Title level={2}>Personal trainer</Title>
      <header className="App-header">
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['']} >
            <Menu.Item key="1"><Link to = "/"  >Home</Link>{' '}</Menu.Item>
            <Menu.Item key="2"><Link to = "/components/Customerlist">All customers</Link>{' '}</Menu.Item>
            <Menu.Item key="3"><Link to = "/components/Traininglist">Trainings</Link>{' '}</Menu.Item>
            <Menu.Item key="4"><Link to = "/components/Customertraining">Trainings with customers</Link>{' '}</Menu.Item>
            <Menu.Item key="5"><Link to = "/components/Tcalendar">Calendar</Link>{' '}</Menu.Item>
        </Menu>
      </header>

      <Switch>
        <Route exact path = "/" component = {Home}/>
        <Route path = "/components/Customerlist" component = {Customerlist}/>
        <Route path = "/components/Traininglist" component = {Traininglist}/>
        <Route path = "/components/Customertraining" component = {Customertraining}/>
        <Route path = "/components/Tcalendar" component = {Tcalendar}/>
        <Route render = {() => <Title level={2}>Page not found</Title>}/>
      </Switch>
    </div>
  </BrowserRouter>
  </div>
  );
}

export default App;

